import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import { Helmet } from "react-helmet";


const HeaderScript = () => {
   const authorized = useSelector(state => state.authorized)
   const userDetail = authorized?.userData?.user_data
   useEffect(() => {
   }, [authorized, userDetail])
   return <>
         {!JSON.parse(localStorage.getItem('isAdmin')) && userDetail?.Intercom && Object.keys(userDetail?.Intercom).length > 0 && !userDetail.Intercom.email.includes('taggbox.com') ?
            <Helmet script={[{
               type: 'text/javascript',
               innerHTML: `window._mfq = window._mfq || [];
                        (function() {
                         var mf = document.createElement("script");
                         mf.type = "text/javascript";
                         mf.async = true;
                         mf.src = "//cdn.mouseflow.com/projects/177a39e9-5702-4789-9d39-064b44841fc2.js";
                         document.getElementsByTagName("head")[0].appendChild(mf);
                        })();
                        window._mfq.push(["setVariable", "UserName", "${userDetail.Intercom.email}"]);
                        window._mfq.push(["setVariable", "PlanName", "${userDetail.Intercom.plan}"]);
                        window._mfq.push(["setVariable", "UserType", "Cake"]);`}]}>

            </Helmet>
            : <></>}

         {
            !JSON.parse(localStorage.getItem('isAdmin')) && userDetail?.Intercom && Object.keys(userDetail?.Intercom).length > 0 && !(userDetail?.Intercom.email.includes('taggbox.com') || userDetail?.UserPlan.planId == '57' || userDetail?.UserPlan.planId == '1' || userDetail?.user.is_trial == 1) ?
               <Helmet script={[{
                  type: 'text/javascript',
                  innerHTML: `zapscale.init("640b054a18f6a72d02e1d8c1", {
                  product_name: "Tagshop",             // ---> name of the product
                  organization_id: "${userDetail.user.id}",          // ---> id of the company/organization
                  organization_name: "${userDetail.Intercom.name} ${userDetail.Intercom.email}",       // ---> organization's name
                  unique_organization_id: "${userDetail.user.id}",  // ---> unique id of the company/organization (optional)
                  user_id: "${userDetail.user.id}",                // ---> unique id of the authenticated user
                  user_name: "${userDetail.Intercom.name}",              // ---> user's name
                  user_email: "${userDetail.Intercom.email}",            // ---> user's email
                  role_id: "${userDetail.Intercom.plan}",               // ---> unique id of the user's role
                  role_name: "${userDetail.Intercom.plan}",            // ---> role name
                  }, {
                  development: false               // ---> (optional) set it to true in development/staging environment
                  });`
               }]} > </Helmet>
               : <></>
         }
      </>
}

export default HeaderScript